/* font-face start
----------------------------*/
@font-face
{ 
	font-family: 'Lucida Bright Regular';
	src:url('../fonts/Lucida Bright Regular.eot') format('eot'), 
		url('../fonts/Lucida Bright Regular.svg') format('svg'), 	
		url('../fonts/Lucida Bright Regular.ttf') format('ttf'), 
		url('../fonts/Lucida Bright Regular.woff') format('woff'), 
		url('../fonts/Lucida Bright Regular.woff2') format('woff2');
		font-weight: 400;
}

/* reset css start
----------------------------*/
*{ box-sizing:border-box;}
body{ padding:0; margin:0; scroll-behavior:smooth;background-color: #233e56;}
h1,h2,h3,h4,h5,h6{ margin:0; padding:0}
figure{ padding:0; margin:0;}
img{margin: 0; padding: 0;max-width: 100%;}
ul,li{ text-decoration:none; display:inline; margin:0; padding:0;}
a{ text-decoration:none; text-decoration-line: none;display: inline-block;}
a:hover{text-decoration:none; text-decoration-line: none;}
p{ margin:0; padding:0}	 


/* default SCSS start
----------------------------*/

$header-bg: #112434;
$social-hover: #ffd200; 
$h1-fonts: Lucida Bright Regular;


/* default css start
----------------------------*/

body{
	padding: 75px;
	position: relative;
}
.slick-slide
{
	outline: none; 
}
.slick-list
{
	overflow: visible;
}
section#pageWrapper
{
	position: relative;
}
.container
{
	width: 100%;
	max-width: 1680px;
}

/* Header Section
----------------------------*/

.pageheader
{
	position: absolute;
    z-index: 10;
    background-color: #112434;
    width: 15.3%; 
    height: 100%;

	.logo
	{
		position: absolute;
	    top: 5%;
	    left: 23%;
	}

	.social-links {
	    position: absolute;
	    bottom: 3.7%;
	    left: 32%;
	    transform: translate(-50%, 0%);
	
		.link-border
		{
			border: 1px solid #fff;
			border-radius: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 10px;
			margin: 20px 0px;
			cursor: pointer;

			i
			{
				color: #fff;
				font-size: 24px;
			}
		}

		.link-border:hover 
		{
			border: 1px solid $social-hover;
		}

		.link-border:hover i
		{
			color: $social-hover;
		}
	}
}

/* Banner Section
----------------------------*/

.banner-image
{
	position: relative;

	figure img
	{
		display: flex;
	}
}
.banner-image:after
{
	content: "";
    position: absolute;
    top: 0;
    right: 1px;
    background-color: #fccf00;
    width: 33.3%;
    height: 89%;
    z-index: 11;
}
.menu
{
    position: absolute;
    top: 5%;
    right: 4%;
    z-index: 21;

	ul li{
		padding: 0px 27px;
		
		a figure img {
   			width: 100%;
    		max-width: 100%; 
		}
	}

	ul li:last-child 
	{
    	padding-left: 30px;
	}
}


