.banner-section
{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 15;
	width: 80%;
	max-width: 100%;
	
	.item
	{
		display: flex !important;
	}

	.banner-title
	{
		width: 100%;
		max-width: 50%;
		color: #fff;

		h1
		{
			font-family: $h1-fonts;
			font-size: 110px;
			font-weight: 400;
			text-decoration: underline #ffd200; 
		}

		p
		{
			width: 50%;
			font-size: 25px;
		}
	}
}

.item-main-image {
    width: 100%;
    max-width: 50%;
}

button.slick-arrow {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-7px,-50%);
    width: 140px;
    height: 120px;
    background: url(../images/back.png) no-repeat;
    text-indent: 200px;
    background-color: #fccf00;
    background-position: 20px 45px;
    overflow: hidden;
    border: 0;
    z-index: 10;
    outline: none;
}
button.slick-next.slick-arrow
{
	left: inherit;
    right: 0;
    -webkit-transform: translateX(7px) rotate(180deg);
    -ms-transform: translateX(7px) rotate(180deg);
    transform: translateX(7px) rotate(180deg);
}


.item-second-image{
	position: absolute;
	bottom: -10%;
	left: 0;
}
